import * as React from 'react';



export default function Errors(props: any){

    return props.errors && <div className="form-errors" style={{fontWeight:"bold"}}>

    {/* return props.errors && <div className="form-errors" style={{ color:"red", fontWeight:"bold"}>         */}

    {
        props.errors.map((error: any, key: any) => <div className="error-message"key={key}>
            <p >{error}</p>
        </div>)
    }
    </div>

}