import React from 'react';
//import VerticlaMenu from "./components/VerticlaMenu";
import * as api from './api';
import LoginPage from './components/Usuarios/LoginPage';
import "./assets/scss/material-kit-react.scss?v=1.10.0";
import "./assets/css/material-dashboard-react.css?v=1.10.0";
//import "react-perfect-scrollbar/dist/css/styles.css";
//import PerfectScrollbar from "react-perfect-scrollbar";
import {host} from './conf';
import { Route, useHistory } from "react-router-dom";
//import Admin from "./layouts/Admin.js";
import Principal from './components/Principal';
import DetalleAnulacion from './components/DetalleAnulacion';

function App() {
  const [login, updateLogin] = React.useState(false);
  
  const [rol, updateRol] = React.useState(false);
  const [user, updateUser] = React.useState(false);
  
  const [rolCurrent, setRolCurrent] = React.useState('');
  const [userCurrent, setUserCurrent] = React.useState('');
  
  const [notify, setNotify] = React.useState({isOpen: false, message: '', errors: [], type: ''});
  const history = useHistory();  
   
  
  React.useEffect(() => {
    const data = sessionStorage.getItem('session-token');       
    if (data) {
      updateLogin(true);                        
    }   
  }, []);
  React.useEffect(() => {
    const data = sessionStorage.getItem('session-rol');       
    if (data) {
      setRolCurrent(data)  
      updateRol(true);                  
    }   
  },[]);
  React.useEffect(() => {
    const data = sessionStorage.getItem('session-user');       
    if (data) {
      setUserCurrent(data)  
      updateUser(true);                   
    }   
  },[]);
 
  React.useEffect(() => {
    localStorage.setItem('session-token', JSON.stringify(login));
    localStorage.setItem('session-rol', JSON.stringify(rol));
    localStorage.setItem('session-user', JSON.stringify(user));    
  }); 
  
  const updateAll = ()=> {
    updateRol(true);
    updateUser(true);  
  }
  
  const logOut =  () => api.postData(host+'/rest-auth/logout/').then((data: any) => {
        if(data.status===401){
            sessionStorage.removeItem('session-token');
            sessionStorage.removeItem('session-rol');
            sessionStorage.removeItem('session-user');
            updateLogin(false);
            updateUser(false);
            updateRol(false);    
            history.push('/');
        }
        if(data.status !== 200){
          setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos ', errors: [], type: 'error'});
        } else {
            sessionStorage.removeItem('session-token');
            sessionStorage.removeItem('session-rol');
            sessionStorage.removeItem('session-user');
            updateLogin(false);
            updateUser(false);
            updateRol(false);    
            history.push('/');
        }
  }).catch( e=>
    { 
      setNotify({isOpen: true, message: 'Error Grave en el Servidor: No se pueden cargar datos ', errors: [], type: 'error'});
    });

  return (
    <div >   
      {/* ROL USUARIO {rolCurrent} */}
      {
      //   !login?
      //  <Route path={process.env.PUBLIC_URL + '/'} render={() => <LoginPage updateLogin = {updateLogin} updateRol = {updateRol} updateUser = {updateUser} setRolCurrent = {setRolCurrent} setUserCurrent = {setUserCurrent}/>} />:
      //   <Route path={process.env.PUBLIC_URL + '/'} render={() => <Principal login = {login} logOut = {logOut} rolCurrent = {rolCurrent} userCurrent = {userCurrent} updateAll = {updateAll}/>} /> //

        <Route path={process.env.PUBLIC_URL + '/'} render={() => <DetalleAnulacion/>}/>
      }

    </div>
    
  );
}

export default App;
